import React, { useState, useEffect } from "react"
import { Badge, Col, Container, Row, Table } from 'react-bootstrap';
import Img from 'gatsby-image';
import ReactNavBar from "../components/navbar"
import { graphql } from "gatsby";
import styles from "./ws-template-modules.module.css"

const WeatherStationTitle = props => (
    <div>
        <h3 className={styles.title}>
            {props.name} <Badge variant="secondary">New</Badge>
        </h3>
    </div>
)

const WeatherStationRow = props => (
    <tr>
        <td>
            {props.description}
        </td>
        <td>
            {props.current}{props.measure}
        </td>
        <td>
            {props.minValue}{props.measure} alle {props.minTime}
        </td>
        <td>
            {props.maxValue}{props.measure} alle {props.maxTime}
        </td>
    </tr>
)

const WeatherStationRowSingleValue = props => (
    <tr>
        <td>
            {props.description}
        </td>
        <td>
            {props.current}{props.measure}
        </td>
        <td>
            -
        </td>
        <td>
            -
        </td>
    </tr>
)

const TableHead = () => (
    <thead>
        <tr>
            <th scope="col">Sensore</th>
            <th scope="col">Attuale</th>
            <th scope="col">Minimo</th>
            <th scope="col">Massimo</th>
        </tr>
    </thead>
)

export default function WSTemplate({ pageContext, data }) {
    const weatherStation = pageContext
    // ----------------------
    // RUNTIME DATA FETCHING
    // ----------------------
    // Temperature
    const [temperature, setTemperature] = useState(0)
    const [temperatureMinValue, setTemperatureMinValue] = useState(0)
    const [temperatureMinTime, setTemperatureMinTime] = useState(0)
    const [temperatureMaxValue, setTemperatureMaxValue] = useState(0)
    const [temperatureMaxTime, setTemperatureMaxTime] = useState(0)
    // Humidity
    const [humidity, setHumidity] = useState(0)
    const [humidityMinValue, setHumidityMinValue] = useState(0)
    const [humidityMinTime, setHumidityMinTime] = useState(0)
    const [humidityMaxValue, setHumidityMaxValue] = useState(0)
    const [humidityMaxTime, setHumidityMaxTime] = useState(0)
    // Pressure
    const [pressure, setPressure] = useState(0)
    const [pressureMinValue, setPressureMinValue] = useState(0)
    const [pressureMinTime, setPressureMinTime] = useState(0)
    const [pressureMaxValue, setPressureMaxValue] = useState(0)
    const [pressureMaxTime, setPressureMaxTime] = useState(0)
    // Dew point
    const [dewPoint, setDewPoint] = useState(0)
    const [dewPointMinValue, setDewPointMinValue] = useState(0)
    const [dewPointMinTime, setDewPointMinTime] = useState(0)
    const [dewPointMaxValue, setDewPointMaxValue] = useState(0)
    const [dewPointMaxTime, setDewPointMaxTime] = useState(0)
    // Heat index
    const [heatIndex, setHeatIndex] = useState(0)
    const [heatIndexMinValue, setHeatIndexMinValue] = useState(0)
    const [heatIndexMinTime, setHeatIndexMinTime] = useState(0)
    const [heatIndexMaxValue, setHeatIndexMaxValue] = useState(0)
    const [heatIndexMaxTime, setHeatIndexMaxTime] = useState(0)
    // Wind chill
    const [windChill, setWindChill] = useState(0)
    const [windChillMinValue, setWindChillMinValue] = useState(0)
    const [windChillMinTime, setWindChillMinTime] = useState(0)
    const [windChillMaxValue, setWindChillMaxValue] = useState(0)
    const [windChillMaxTime, setWindChillMaxTime] = useState(0)
    useEffect(() => {
    // get data from weather station endpoint
    fetch(`https://datimeteoasti-poc.herokuapp.com/rawdata`)
        .then(response => response.json()) // parse JSON from request
        .then(resultData => {
            // Set temperature from fetched data
            setTemperature(resultData[0].info.temperature.current)
            setTemperatureMinValue(resultData[0].info.temperature.min.value)
            setTemperatureMinTime(resultData[0].info.temperature.min.time)
            setTemperatureMaxValue(resultData[0].info.temperature.max.value)
            setTemperatureMaxTime(resultData[0].info.temperature.max.time)
            // Set humidity from fetched data
            setHumidity(resultData[0].info.humidity.current)
            setHumidityMinValue(resultData[0].info.humidity.min.value)
            setHumidityMinTime(resultData[0].info.humidity.min.time)
            setHumidityMaxValue(resultData[0].info.humidity.max.value)
            setHumidityMaxTime(resultData[0].info.humidity.max.time)
            // Set dew pont from fetched data
            setDewPoint(resultData[0].info.dew_point.current)
            setDewPointMinValue(resultData[0].info.dew_point.min.value)
            setDewPointMinTime(resultData[0].info.dew_point.min.time)
            setDewPointMaxValue(resultData[0].info.dew_point.max.value)
            setDewPointMaxTime(resultData[0].info.dew_point.max.time)
            // Set heat index from fetched data
            setHeatIndex(resultData[0].info.heat_index.current)
            setHeatIndexMinValue(resultData[0].info.heat_index.min.value)
            setHeatIndexMinTime(resultData[0].info.heat_index.min.time)
            setHeatIndexMaxValue(resultData[0].info.heat_index.max.value)
            setHeatIndexMaxTime(resultData[0].info.heat_index.max.time)
            // Set wind chill from fetched data
            setWindChill(resultData[0].info.wind_chill.current)
            setWindChillMinValue(resultData[0].info.wind_chill.min.value)
            setWindChillMinTime(resultData[0].info.wind_chill.min.time)
            setWindChillMaxValue(resultData[0].info.wind_chill.max.value)
            setWindChillMaxTime(resultData[0].info.wind_chill.max.time)
            // Set pressure from fetched data
            setPressure(resultData[0].info.pressure.current)
            setPressureMinValue(resultData[0].info.pressure.min.value)
            setPressureMinTime(resultData[0].info.pressure.min.time)
            setPressureMaxValue(resultData[0].info.pressure.max.value)
            setPressureMaxTime(resultData[0].info.pressure.max.time)
        }) // set data for the number of stars
    }, [weatherStation])

    return (
        <Container>
            {/* Check how to globally include this CSS from a CDN in gatsby */}
            <link
                rel="stylesheet"
                href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
                integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
                crossOrigin="anonymous"
            />
            <Row>
                <Col>
                    <ReactNavBar />
                </Col>
            </Row>
            <Row>
                <Col></Col>
                <Col></Col>
                <Col>
                    <Img fluid={data.headerImage.childImageSharp.fluid} alt="datimeteoasti" />
                </Col>
                <Col></Col>
                <Col></Col>
            </Row>
            <Row>
                <Col>
                    <WeatherStationTitle name="Osservatorio meteorologico di ASTI nord, zona Ospedale C. Massaia - 155 m s. l. m." />
                    <Table striped bordered hover>
                        <TableHead />
                        <tbody>
                            <WeatherStationRow
                                description="Temperatura"
                                current={temperature}
                                minValue={temperatureMinValue}
                                minTime={temperatureMinTime}
                                maxValue={temperatureMaxValue}
                                maxTime={temperatureMaxTime}
                                measure="C"
                            />
                            <WeatherStationRow
                                description="Umidit"
                                current={humidity}
                                minValue={humidityMinValue}
                                minTime={humidityMinTime}
                                maxValue={humidityMaxValue}
                                maxTime={humidityMaxTime}
                                measure="%"
                            />
                            <WeatherStationRow
                                description="Dew point"
                                current={dewPoint}
                                minValue={dewPointMinValue}
                                minTime={dewPointMinTime}
                                maxValue={dewPointMaxValue}
                                maxTime={dewPointMaxTime}
                                measure="C"
                            />
                            <WeatherStationRow
                                description="Heat index"
                                current={heatIndex}
                                minValue={heatIndexMinValue}
                                minTime={heatIndexMinTime}
                                maxValue={heatIndexMaxValue}
                                maxTime={heatIndexMaxTime}
                                measure="C"
                            />
                            <WeatherStationRow
                                description="Wind chill"
                                current={windChill}
                                minValue={windChillMinValue}
                                minTime={windChillMinTime}
                                maxValue={windChillMaxValue}
                                maxTime={windChillMaxTime}
                                measure="C"
                            />
                            <WeatherStationRow
                                description="Pressione"
                                current={pressure}
                                minValue={pressureMinValue}
                                minTime={pressureMinTime}
                                maxValue={pressureMaxValue}
                                maxTime={pressureMaxTime}
                                measure=" hPa"
                            />
                            <WeatherStationRowSingleValue
                                description="Direzione vento"
                                current="SW"
                                min="-"
                                max="-"
                            />
                        </tbody>
                    </Table>
                </Col>
            </Row>
        </Container>
        
    )
}

export const query = graphql`
query {
  headerImage: file(relativePath: {eq: "datimeteoasti.jpg"}) {
    childImageSharp {
      fluid(maxWidth: 300) {
        aspectRatio
        src
        originalName
        base64
        originalImg
        srcSet
        srcSetWebp
        srcWebp
        sizes
      }
    }
  }
}
`